import React from 'react'
import styled from '@emotion/styled'
import { css } from '@emotion/core'
import { StaticQuery, graphql } from 'gatsby'
import Img  from 'gatsby-image'
import { Row, Col, Typography, Container } from 'reus'

const Quote = () => {
  const TestimonyWrapper = styled.div`
    padding: 50px 0;
  `
  const StyledImage = styled(Img)`
    border-radius: 50%;
    overflow: hidden;
    width: 215px;
    height: 215px;
    background: #eaeaea;
    @media screen and (max-width: ${({theme}) => theme.breakPoints.sm}) {
      flex-direction: column-reverse;
      height: 120px;
      width: 120px;
      margin: 0 auto;
      margin-bottom: 30px;
    }
  `
  const SpanQuote = styled.span`
    position: absolute;
    top: -90px;
    font-size: 140px;
    left: -7px;
    color: #dedede;
  `
  return (
    <StaticQuery
      query={graphql`
        query QuoteQuery {
          people: file(relativePath: { eq: "attendance-leave-modana.png" }) {
            childImageSharp {
              fluid(maxWidth: 182) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      `}
      render={data => (
        <TestimonyWrapper>
          <Container xl gutter={22}>
            <Row align="center" gutter={20} css={theme => css`
              @media screen and (max-width: ${theme.breakPoints.sm}) {
                flex-direction: column-reverse;
              }
            `}>
              <Col md={9}>
                <div>
                  <Typography
                    type="quote-reduced"
                    color="#444"
                    tag="h4">
                    <SpanQuote>"</SpanQuote>That would have been a nightmare to do manually. But with  Modana, it was easy.
                  </Typography>
                  <Typography
                    type="body"
                    css={theme => css`
                      color: ${theme.color.primary};
                      margin: 0;
                    `}
                    tag="p">
                    Peter Spiderman
                  </Typography>
                </div>
              </Col>
              <Col md={3}>
                <div>
                  <StyledImage fluid={data.people.childImageSharp.fluid}/>
                </div>
              </Col>
            </Row>
          </Container>
        </TestimonyWrapper>
      )}
    />
  )
}

export default Quote